
body {
  background-color: #ddd1e7;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  padding-bottom: 50px;
  color: #2c3e50;
}
#app .page-head {
    background-color: #aa8cc5;
    color: #fff;
}
#app .hello-world {
    background-color: #d3d3d3;
}
#app .chart-view {
    width: 70%;
    margin: 20px auto;
}
#app .chart-view .chart {
      max-width: 600px;
      margin: 50px auto;
}
#app .chart-view .map, #app .chart-view .table-div {
      margin-top: 100px;
      width: 100%;
}

.login-page {
  width: 30%;
  margin: auto;
  padding-bottom: 30px;
}
.login-page .form {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    margin: 0 auto;
    padding: 45px;
    text-align: center;
    -webkit-box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
            box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
.login-page .form input {
      font-family: "Roboto", sans-serif;
      outline: 0;
      background-color: #f2f2f2;
      width: 100%;
      border: 0;
      margin: 0 0 15px;
      padding: 15px;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      font-size: 14px;
}
.login-page .form button {
      font-family: "Roboto", sans-serif;
      text-transform: uppercase;
      outline: 0;
      width: 100%;
      border: 0;
      padding: 15px;
      color: #FFFFFF;
      font-size: 14px;
      -webkit-transition: all 0.3 ease;
      transition: all 0.3 ease;
      cursor: pointer;
}
.login-page .form .login-btn {
      background: #aa8cc5;
      margin: 10px 0px;
}
.login-page .social-link span {
    display: inline-block;
}
.login-page .error {
    color: #ff0000;
}

